import PropTypes from 'prop-types'
import React from 'react'
import Slider from '../../components/Slider'
import { Grid, GridContainer, GridColumn } from '../../components/Grid'
import { Image } from '../../components/Image'
import { Title } from '../../components/Title'
import './image-text-slider.scss'

function ImageTextSlider({ settings, slides }) {
  const defaultSettings = {
    ...settings,
    appendDots: SliderDots,
    slidesToShow: 1,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1280,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  }
  return (
    <GridContainer className="image-text-slider">
      <Slider settings={defaultSettings}>
        {slides &&
          slides.map((slide) => (
            <div key={slide.title}>
              <Grid>
                <GridColumn columns="7" className="image-text-slider__image">
                  <Image src={slide.image} variant="imageTextSlider" />
                </GridColumn>
                <GridColumn columns="5">
                  <Title variant="boxed" className="image-text-slider__title">
                    {slide.title}
                  </Title>
                  <Title
                    className="image-text-slider__description"
                    variant="medium"
                  >
                    {slide.description}
                  </Title>
                </GridColumn>
              </Grid>
            </div>
          ))}
      </Slider>
    </GridContainer>
  )
}

ImageTextSlider.propTypes = {
  settings: PropTypes.object,
  slides: PropTypes.array.isRequired,
}

export default ImageTextSlider

function SliderDots(dots) {
  return <div className="slider__dots">{dots}</div>
}
