import PropTypes from 'prop-types'
import React from 'react';
import { Title, TitleAccentLine } from '../../components/Title'
import { Grid, GridColumn, GridContainer } from '../../components/Grid'
import TeamMember from '../../components/TeamMember'
import './team-block.scss';

function Team({ title, subtitle, team }) {
  return (
    <GridContainer className="team-block">
      <Grid variant="centered">
        <GridColumn columns="10">
          <Grid>
            <Title variant="lead" className="team-block__title">
              {title}
              <TitleAccentLine>{subtitle}</TitleAccentLine>
            </Title>

            <GridColumn columns="12">
              <Grid variant="centered">
                {team &&
                  team.map((member) => (
                    <GridColumn
                      columns="auto"
                      className="team-block__name"
                      key={member.name}>
                      <TeamMember
                        name={member.name}
                        lastname={member.lastname}
                        title={member.title}
                        image={member.image}
                      />
                    </GridColumn>
                  ))}
              </Grid>
            </GridColumn>
          </Grid>
        </GridColumn>
      </Grid>
    </GridContainer>
  )
}

Team.propTypes = {
  title: PropTypes.string.isRequired,
  subtitle: PropTypes.string.isRequired,
  team: PropTypes.array.isRequired,
}

export default Team
