import React from 'react'
import PropTypes from 'prop-types'
import { Grid, GridColumn } from '../Grid'
import { Title, TitleAccentLine } from '../Title'
import { Image } from '../Image'
import Paper from '../Paper'
import "./team-member.scss";

function TeamMember({ name, lastname, title, image }) {
  return (
    <div className="team-member">
      <Grid variant="middle" className="team-member__grid">
        <GridColumn columns="5" sx={{ zIndex: 5 }} className="team-member__left">
          <Grid variant="right">
            <Paper
              variant="image"
              className="team-member__image-wrapper">
              <Image src={image} />
            </Paper>
          </Grid>
        </GridColumn>
        <GridColumn columns="7" className="team-member__right">
          <Paper
            variant="plainGray"
            className="team-member__content">
            <Title className="team-member__name">
              {name}
              <TitleAccentLine>{lastname}</TitleAccentLine>
            </Title>
            <p className="team-member__title">{title}</p>
          </Paper>
        </GridColumn>
      </Grid>
    </div>
  )
}

TeamMember.propTypes = {
  name: PropTypes.string.isRequired,
  lastname: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
}

export default TeamMember
